:root {
  --background: #ffffff;
  --foreground: #171717;
  --mint-green: #d1fae5;
  --sky-blue: #dbeafe;
  --beige: #fef3c7;
  --off-white: #f9fafb;
  --emerald-green: #059669;
  --deep-blue: #1e40af;
  --sunflower: #fbbf24;
  --coral: #f87171;
  --lavender: #c7d2fe;
  --placeholder-gray: #4b5563;
  --darker-gray: #374151;
}
@media (prefers-color-scheme: dark) {
  :root {
    --background: #0a0a0a;
    --foreground: #ededed;
  }
}
body {
  background: var(--background);
  color: var(--foreground);
  font-family: var(--font-sans);
  overflow-x: hidden;
}
.bg-mint-green {
  background-color: var(--mint-green);
}
.bg-sky-blue {
  background-color: var(--sky-blue);
}
.bg-beige {
  background-color: var(--beige);
}
.bg-off-white {
  background-color: var(--off-white);
}
.text-emerald-green {
  color: var(--emerald-green);
}
.text-deep-blue {
  color: var(--deep-blue);
}
.text-dark-label {
  color: var(--darker-gray);
}
::-webkit-input-placeholder {
  color: var(--placeholder-gray);
  opacity: 1;
}
::-moz-placeholder {
  color: var(--placeholder-gray);
  opacity: 1;
}
:-ms-input-placeholder {
  color: var(--placeholder-gray);
  opacity: 1;
}
:-moz-placeholder {
  color: var(--placeholder-gray);
  opacity: 1;
}
::placeholder {
  color: var(--placeholder-gray);
  opacity: 1;
}
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes pulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes float {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-10px);
  }
  100% {
    transform: translateY(0px);
  }
}
@keyframes bounce {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
}
@keyframes shimmer {
  0% {
    background-position: -200% 0;
  }
  100% {
    background-position: 200% 0;
  }
}
.animate-fade-in-up {
  animation: fadeInUp 0.6s ease-out forwards;
}
.animate-pulse-slow {
  animation: pulse 3s infinite;
}
.animate-float {
  animation: float 4s ease-in-out infinite;
}
.animate-bounce {
  animation: bounce 2s infinite;
}
.animation-delay-200 {
  animation-delay: 0.2s;
}
.animation-delay-400 {
  animation-delay: 0.4s;
}
.animation-delay-600 {
  animation-delay: 0.6s;
}
.animation-delay-800 {
  animation-delay: 0.8s;
}
.animation-delay-1000 {
  animation-delay: 1s;
}
.animation-delay-2000 {
  animation-delay: 2s;
}
.animation-delay-4000 {
  animation-delay: 4s;
}
.bg-gradient-diagonal {
  background: linear-gradient(135deg, var(--mint-green) 0%, var(--sky-blue) 100%);
}
.bg-gradient-radial {
  background: radial-gradient(circle at top right, var(--mint-green) 0%, var(--sky-blue) 100%);
}
.bg-gradient-hero {
  background: linear-gradient(135deg, #e0f2fe 0%, #dcfce7 100%);
}
.shadow-soft {
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.05);
}
.shadow-soft-lg {
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.08);
}
.shadow-soft-xl {
  box-shadow: 0 20px 50px rgba(0, 0, 0, 0.1);
}
.hover-lift {
  transition: all 0.3s ease;
}
.hover-lift:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1);
}
.btn-primary {
  background: linear-gradient(135deg, var(--emerald-green) 0%, #047857 100%);
  color: white;
  border: none;
  padding: 12px 24px;
  border-radius: 50px;
  font-weight: 600;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(5, 150, 105, 0.3);
}
.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(5, 150, 105, 0.4);
}
.btn-secondary {
  background: transparent;
  color: var(--emerald-green);
  border: 2px solid var(--emerald-green);
  padding: 12px 24px;
  border-radius: 50px;
  font-weight: 600;
  transition: all 0.3s ease;
}
.btn-secondary:hover {
  background: var(--emerald-green);
  color: white;
  transform: translateY(-2px);
}
.shimmer {
  background: linear-gradient(to right, #f6f7f8 0%, #edeef1 20%, #f6f7f8 40%, #f6f7f8 100%);
  background-size: 200% 100%;
  animation: shimmer 1.5s infinite linear;
}
.hide-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.hide-scrollbar::-webkit-scrollbar {
  display: none;
}
